@font-face {
  font-weight: 700;
  font-family: "Roboto";
  font-style: normal;
  src: url(../../fonts/Roboto-Bold.woff2) format("woff2"), url(../../fonts/Roboto-Bold.woff) format("woff");
  font-display: swap;
}

@font-face {
  font-weight: 400;
  font-family: "Roboto";
  font-style: italic;
  src: url(../../fonts/Roboto-Italic.woff2) format("woff2"), url(../../fonts/Roboto-Italic.woff) format("woff");
  font-display: swap;
}

@font-face {
  font-weight: 300;
  font-family: "Roboto";
  font-style: normal;
  src: url(../../fonts/Roboto-Light.woff2) format("woff2"), url(../../fonts/Roboto-Light.woff) format("woff");
  font-display: swap;
}

@font-face {
  font-weight: 500;
  font-family: "Roboto";
  font-style: normal;
  src: url(../../fonts/Roboto-Medium.woff2) format("woff2"), url(../../fonts/Roboto-Medium.woff) format("woff");
  font-display: swap;
}

@font-face {
  font-weight: 500;
  font-family: "Roboto";
  font-style: italic;
  src: url(../../fonts/Roboto-MediumItalic.woff2) format("woff2"), url(../../fonts/Roboto-MediumItalic.woff) format("woff");
  font-display: swap;
}

@font-face {
  font-weight: 400;
  font-family: "Roboto";
  font-style: normal;
  src: url(../../fonts/Roboto-Regular.woff2) format("woff2"), url(../../fonts/Roboto-Regular.woff) format("woff");
  font-display: swap;
}

@font-face {
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-style: normal;
  src: url(../../fonts/RobotoCondensed-Bold.woff2) format("woff2"), url(../../fonts/RobotoCondensed-Bold.woff) format("woff");
  font-display: swap;
}

html {
  height: 100%;
}

body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-width: 1900px;
  min-height: 100%;
  margin: 0;
  font-size: 24px;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

.title {
  margin: 0;
  font-weight: 700;
  font-size: 128px;
  line-height: 1.17;
  font-family: "Roboto Condensed", sans-serif;
  text-transform: uppercase;
}

.primary {
  color: #f5333f;
}

.section {
  padding: 98px 0;
}

.container {
  box-sizing: border-box;
  max-width: 1880px;
  margin: 0 auto;
  padding-right: 40px;
  padding-left: 40px;
}

.container--dark {
  max-width: 1800px;
  padding: 101px 77px;
  color: #ffffff;
  background-color: #212121;
  border-radius: 20px;
}

.list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.list li {
  position: relative;
  padding-left: 20px;
}

.list li::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 0;
  width: 10px;
  height: 10px;
  background-color: #f5333f;
  border-radius: 50%;
}

.db {
  display: block;
}

.button {
  display: inline-block;
  padding: 10px 25px;
  font-weight: 500;
  font-size: 20px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #f5333f;
  border: 0;
  border-radius: 5px;
  appearance: none;
}

.button:not(:disabled):hover {
  cursor: pointer;
  opacity: 0.8;
}

.button:active {
  opacity: 0.4;
}

.button--transparent {
  padding: 9px 24px;
  color: #f5333f;
  background-color: transparent;
  border: 1px solid #f5333f;
}

.header__top {
  border-bottom: 1px solid #c2c2c2;
}

.header__top .container {
  display: flex;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 29px;
}

.header__bottom .container {
  padding: 148px 650px 147px 40px;
}

.header__logo {
  margin-right: 120px;
}

.header__top-button {
  margin-left: auto;
}

.header__bottom {
  background-image: url("../../img/waves-top.png");
  background-image: image-set(url("../../img/waves-top.avif") type("image/avif"), url("../../img/waves-top.png"));
  background-repeat: no-repeat;
  background-position: right -165px center;
  background-size: auto 100%;
  border-bottom: 1px solid #c2c2c2;
}

.header__title {
  margin-bottom: 10px;
  line-height: 1.17;
}

.header__subtitle {
  margin: 0 0 40px;
  font-weight: 300;
  font-size: 32px;
}

.header__bottom-button {
  padding: 20px 26px;
}

.logo {
  display: block;
  width: fit-content;
}

.logo:hover {
  opacity: 0.8;
}

.logo:active {
  opacity: 0.4;
}

.logo img {
  display: block;
}

.nav__toggle {
  display: none;
}

.nav__list {
  display: flex;
  margin: -4px 0 0 0;
  padding: 0;
  list-style-type: none;
  gap: 70px;
}

.nav__link {
  font-size: 20px;
  color: inherit;
  text-decoration: none;
}

.nav__link:hover {
  opacity: 0.8;
}

.nav__link:active {
  opacity: 0.4;
}

.clients {
  border-bottom: 1px solid #c2c2c2;
}

.clients__container {
  padding-top: 39px;
  padding-bottom: 40px;
}

.clients__container > * {
  margin-top: 0;
}

.clients__container > *:last-child {
  margin-bottom: 0;
}

.clients__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, 180px);
  align-items: center;
  padding: 0 20px;
  list-style-type: none;
  gap: 68px 136px;
}

.clients__list img {
  display: block;
}

.clients__link:hover {
  opacity: 0.8;
}

.clients__link:active {
  opacity: 0.4;
}

.services {
  padding-bottom: 99px;
  background-color: #fafafa;
  background-image: url("../../img/waves.png");
  background-image: image-set(url("../../img/waves.avif") type("image/avif"), url("../../img/waves.png"));
  background-repeat: no-repeat;
  background-position: left -6px center;
  background-size: auto 100%;
  border-bottom: 1px solid #d1d1d1;
}

.services__container {
  display: grid;
  align-items: center;
  grid-template-columns: 850px auto;
  gap: 116px;
}

.services__list {
  gap: 24px;
}

.results {
  padding-bottom: 100px;
  padding-left: 2px;
  background-image: url(../../img/points.png);
  background-position: center top;
  border-bottom: 1px solid #efefef;
}

.results__container {
  padding-top: 2px;
}

.slider-two {
  display: grid;
  grid-template-columns: 887px auto;
  grid-template-rows: auto 1fr;
  gap: 0 176px;
}

.slider-two picture {
  display: block;
}

.slider-two__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.slider-two__item:not(.slider-two__item--active) {
  display: none;
}

.slider-two__list-img {
  grid-row-end: -1;
  grid-row-start: 1;
}

.slider-two__item-img {
  display: grid;
  grid-template-columns: 250px 175px 37px 215px 210px;
  grid-template-rows: 28px 38px 682px 29px 38px;
}

.slider-two__item-img .slider-two__item-picture {
  display: block;
  overflow: hidden;
  border-radius: 25px;
}

.slider-two__item-img .slider-two__item-picture:nth-child(1) {
  grid-column-end: 3;
  grid-column-start: 1;
  grid-row-end: 4;
  grid-row-start: 1;
}

.slider-two__item-img .slider-two__item-picture:nth-child(2) {
  grid-column-end: 5;
  grid-column-start: 2;
  grid-row-end: 5;
  grid-row-start: 2;
}

.slider-two__item-img .slider-two__item-picture:nth-child(3) {
  grid-column-end: -1;
  grid-column-start: 4;
  grid-row-end: -1;
  grid-row-start: 3;
}

.slider-two__list-content {
  margin-top: 149px;
}

.slider-two__item-content .slider-two__item-picture {
  display: block;
  margin-bottom: 30px;
}

.slider-two__item-content-dl {
  display: grid;
  grid-template-columns: 110px auto;
  margin: 23px 0 30px;
  gap: 19px 20px;
}

.slider-two__item-content-dt {
  font-weight: 700;
  color: #f5333f;
}

.slider-two__item-content-dd {
  margin: 0;
}

.slider-two__pagination {
  display: flex;
  grid-column-start: 2;
  grid-row-start: 2;
  margin-top: 60px;
  gap: 60px;
}

.slider-two__pagination-prev,
.slider-two__pagination-next {
  display: block;
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-image: url(../../img/arrow-left.svg);
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid #c2c2c2;
  border-radius: 50%;
}

.slider-two__pagination-next {
  transform: scale(-1, 1);
}

.slider-two__pagination-prev:hover,
.slider-two__pagination-next:hover {
  cursor: pointer;
  opacity: 0.8;
}

.slider-two__pagination-prev:active,
.slider-two__pagination-next:active {
  opacity: 0.4;
}

.slider-two__link:hover {
  opacity: 0.8;
}

.slider-two__link:active {
  opacity: 0.4;
}

.providers {
  padding-bottom: 100px;
  background-color: #fafafa;
  background-image: url("../../img/waves-1.png");
  background-repeat: no-repeat;
  background-position: left -7px center;
  background-size: contain;
  border-bottom: 1px solid #d1d1d1;
}

.providers__container {
  display: grid;
  align-items: center;
  grid-template-columns: 1270px 1fr;
  gap: 45px 91px;
}

.providers__list {
  gap: 28px;
}

.providers__footer {
  display: flex;
  grid-column-end: -1;
  grid-column-start: 1;
}

.providers__footer-text {
  box-sizing: border-box;
  width: 50%;
  margin: 0;
  line-height: 130%;
}

.providers__footer-text:nth-child(odd) {
  padding-right: 100px;
}

.providers__footer-text:nth-child(odd):not(:last-child) {
  border-right: 2px solid #ffffff;
}

.providers__footer-text:nth-child(even) {
  padding-left: 100px;
}

.reviews {
  position: relative;
  min-height: 606px;
  padding-top: 94px;
  overflow: hidden;
  font-size: 20px;
  color: #212121;
}

.reviews::after {
  content: "";
  position: absolute;
  top: calc(50% + 38px);
  left: 51%;
  width: 1435px;
  height: 745px;
  background-image: url(../../img/reviews.png);
  background-image: image-set(url("../../img/reviews.webp") 1x type("image/webp"), url("../../img/reviews-2x.webp") 2x type("image/webp"), url("../../img/reviews.png") 1x, url("../../img/reviews-2x.png") 2x);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transform: translateY(-50%);
}

.reviews__container {
  padding-top: 6px;
}

.reviews cite {
  font-weight: 500;
  font-size: 18px;
  color: #f5333f;
  font-style: italic;
}

.reviews b {
  display: block;
  margin-top: 5px;
  font-weight: 700;
  color: #000000;
}

.reviews p {
  margin: 0 0 10px;
  line-height: 1.2;
}

.reviews__title {
  max-width: 50%;
  margin-bottom: 60px;
}

.reviews__blockquote {
  box-sizing: border-box;
  width: 799px;
  margin: 0;
  padding: 25px 30px 23px 106px;
  background-image: url(../../img/blockquote.svg);
  background-repeat: no-repeat;
  background-position: 30px 25px;
  background-size: 56px 55px;
  border: 1px solid #d1d1d1;
  border-radius: 11px;
  box-shadow: 0 0 33px rgba(0, 0, 0, 0.25);
}

.reviews__blockquote:not(:last-child) {
  margin-bottom: 50px;
}

.subscription {
  background-color: #f5f5f5;
  border-top: 1px solid #d1d1d1;
}

.subscription__container {
  display: grid;
  grid-template-columns: 746px 1fr;
  grid-template-rows: auto 1fr;
  padding-bottom: 97px;
}

.subscription__title {
  margin: 0 0 10px;
}

.subscription__subtitle {
  grid-column-start: 1;
  margin: 0;
  font-weight: 700;
  color: #f5333f;
  text-indent: 4px;
}

.subscription__content {
  grid-column-start: 2;
  grid-row-end: 3;
  grid-row-start: 1;
  line-height: 130%;
}

.subscription__content p {
  margin: 0;
}

.subscription__content p:not(:last-child) {
  margin-bottom: 30px;
}

.subscription__form {
  padding-top: 7px;
}

.form__title {
  margin: 0 0 27px;
  font-weight: 700;
  font-size: 40px;
  font-family: "Roboto Condensed", sans-serif;
  text-transform: uppercase;
}

.form__inner {
  display: flex;
}

.form__inner:not(:last-child) {
  margin-bottom: 16px;
}

.form__input {
  display: block;
  box-sizing: border-box;
  width: 100%;
  height: 53px;
  padding: 15px 30px;
  font-weight: 300;
  font-size: 20px;
  font-family: inherit;
  background-color: #fff;
  border: 0;
  border-radius: 5px 0 0 5px;
}

.form__input:not(:disabled):hover {
  opacity: 0.8;
}

.form__input::placeholder {
  color: #b6b6b6;
  opacity: 1;
}

.form__button {
  padding: 14px 30px;
  letter-spacing: -1px;
  border-radius: 0 5px 5px 0;
}

.form__notes {
  margin: 0;
  font-weight: 300;
  font-size: 20px;
  color: #c2c2c2;
}

.footer {
  color: #ffffff;
  background-color: #212121;
}

.footer__container {
  display: flex;
  align-items: flex-start;
  padding-top: 80px;
  padding-bottom: 80px;
}

.footer__logo {
  margin-right: 140px;
}

.footer__nav {
  display: grid;
  grid-template-columns: repeat(3, auto);
  margin: -4px 0 0 0;
  padding: 0;
  list-style-type: none;
  gap: 13px 80px;
}

.footer__link {
  font-weight: 300;
  font-size: 20px;
  color: #ffffff;
  text-decoration: none;
}

.footer__link:hover {
  opacity: 0.8;
}

.footer__link:active {
  opacity: 0.4;
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 0 auto;
  padding: 0;
  list-style-type: none;
  gap: 40px;
}

.footer__social-link {
  position: relative;
  display: block;
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.footer__social-link:hover {
  opacity: 0.8;
}

.footer__social-link:active {
  opacity: 0.4;
}

.footer__social-link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
}

.footer__social-link--1::before {
  background-image: url(../../img/social/twiter.svg);
}

.footer__social-link--2::before {
  background-image: url(../../img/social/linkedin.svg);
}

.footer__social-link--3::before {
  background-image: url(../../img/social/facebook.svg);
}